.container{
    width: 95vw;
    height: 95vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px;
}

.cuprum{
    font-family: 'Cuprum', sans-serif;
}

.brand-name{
    font-size: 36px;
    color: #eeeeee;
    text-align: center;
}

.brand-date{
    font-size: 25px;
}

.top-nav{
    width: 95%;
    height: 200px;
    margin: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

.button{
    background-color: #ffd369;
    border-radius: 25px;
    width: 150px;
    height: 150px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.value{
    text-align: center;
    display: flex;
    align-items: center;
    font-size: 35px;
    height: 75%;
}

.bar{
    width: 750px;
    height: 750px;
    color: #eeeeee;
    margin: 25px;
}